<template>
  <div class="header row">
    <div class="col-xl-2 col-lg-0 col-sm-0"></div>
    <div class="col-xl-8 col-lg-12 col-sm-0">
      <div class="h-logo">张鹏伟</div>
      <div class="h-nav">
        <div class="h-nav-l" :class="{active:isActive}" @click="activel">项目</div>
        <div class="h-nav-r" :class="{active:!isActive}" @click="activer">简历</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return {
      isActive:true
    }
  },
  methods:{
    activer() {
      this.isActive = false
      this.$router.push({path:'/resume'})
    },
    activel() {
      this.isActive = true
      this.$router.push({path:'/'})
    }
  }
}
</script>

<style scoped>
.header{
  position: fixed;
  top: 0;
  width: 100vw;
  height: 60px;
  padding:0;
  margin: 0;
  float: left;
  background-color: rgb(246, 247, 249);
  z-index: 1;
}
.h-logo{
  float: left;
  font-size: 30px;
  line-height: 60px;
  width: 100px;;
  height: 60px;
}
.h-nav{
  float: right;
  width:180px;
  padding: 10px 0;
  height: 60px;
}
.h-nav div{
  width: 50%;
  float: left;
  font-size: 20px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  border: 2px solid rgb(209, 22, 22);
  background-color:transparent;
  color: rgb(209, 22, 22);
}

.h-nav-l{
  border-top-left-radius:8px;
  border-bottom-left-radius: 8px;
}
.h-nav-r{
  border-top-right-radius:8px;
  border-bottom-right-radius: 8px;
}
.h-nav .active{
  background-color: rgb(209, 22, 22);
  color: rgb(255, 255, 255);
}
</style>




