import Vue from 'vue'
import Router from 'vue-router'


const index  = () => import( '@/pages/index')
const shop  = () => import( '@/pages/shop')
const book  = () => import( '@/pages/book')
const resume  = () => import( '@/pages/resume')
const blog  = () => import( '@/pages/blog')
const shoppc  = () => import( '@/pages/shoppc')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/shop',
      name: 'shop',
      component: shop
    },
    {
      path: '/book',
      name: 'book',
      component: book
    },
    {
      path: '/resume',
      name: 'resume',
      component: resume
    },
    {
      path: '/blog',
      name: 'blog',
      component: blog
    },
    {
      path: '/shoppc',
      name: 'shoppc',
      component: shoppc
    }
  ],
  scrollBehavior(to, from, savedPosition) {
    return {
      x: 0,
      y: 0
    }
  }
})



